import React from 'react';
import { Button, Tag, Space } from 'antd';
import ProList from '@ant-design/pro-list';
import '@ant-design/pro-list/dist/list.css'
import 'antd/dist/antd.css'
import axios from 'axios';

const dataSource = [
  {
    title: '项目一',
    avatar:
      'https://gw.alipayobjects.com/zos/antfincdn/efFD%24IOql2/weixintupian_20170331104822.jpg',
    ToDoNumber: '剩余代办数量100',
  },
  {
    title: '项目二',
    avatar:
      'https://gw.alipayobjects.com/zos/antfincdn/efFD%24IOql2/weixintupian_20170331104822.jpg',
    ToDoNumber: 1,
  },
  {
    title: '项目三',
    avatar:
      'https://gw.alipayobjects.com/zos/antfincdn/efFD%24IOql2/weixintupian_20170331104822.jpg',
    ToDoNumber: 1,
  },
  {
    title: '项目四',
    avatar:
      'https://gw.alipayobjects.com/zos/antfincdn/efFD%24IOql2/weixintupian_20170331104822.jpg',
    ToDoNumber: 1,
  },
];

export default class MainPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      dataSource: dataSource,
      tag: '项目',
    }
  }
  updateData() {
    let url = 'http://localhost:8080/getData'
    axios.get(url).then((response)=>{
      console.log(response)
    })
  }
  render() {
    this.updateData()
    return (
      <div>
        <ProList
            id='main-page-list'
            rowKey="title"
            headerTitle="项目列表"
            toolBarRender={() => {
              return [
                <div>
                  <Button id='startFlickerButton' type='primary'>收到消息</Button>
                  <Button id='GoToWebButton' type="primary">
                    进入主站
                  </Button>
                </div>,
              ];
            }}
            dataSource={this.state.dataSource}
            metas={{
              title: {},
              subTitle: {
                render: () => {
                  return (
                    <Space size={0}>
                      <Tag color="blue">{this.state.tag}</Tag>
                    </Space>
                  );
                },
              },
              description: {dataIndex: 'ToDoNumber'},
              avatar: {},
            }}
          />
      </div>
          )
  }
}